<template>
  <div class="game-list">
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      class="van-clearfix"
    >
      <van-cell v-for="item in list" :key="item.contentId">
        <img :src="item.icon" class="img-left" />
        <div class="text-left">
          <p>{{item.title}}</p>
          <p class="text-detail">
            <span class="download">500+下载</span>
            <span class="size">10980.26m</span>
          </p>
        </div>
        <div class="gift">
          <van-button round color="#f2f0f6">下载</van-button>
        </div>
      </van-cell>
    </van-list>
    <van-pagination 
  v-model="currentPage" 
  :total-items="125" 
  :show-page-size="3" 
  force-ellipses
  v-if="!this.propList"
/>
  </div>
</template>

<script>
import { List, Cell, Button,Pagination } from "vant";
import axios from "axios";
import common from "../../style/common.less";
export default {
  components: {
    [List.name]: List,
    [Pagination.name]: Pagination,
    [Button.name]: Button,
    [Cell.name]: Cell
  },
  props: ["propList"],
  data() {
    return {
      list: [],
      loading: false,
      finished: false
    };
  },

  methods: {
    onLoad() {
      // 异步更新数据
      console.log("this.icon", this.propList.icon);
      console.log("this.propList", this.propList);
      this.loading = true;
      if (this.propList) {
        this.list = this.propList;
        this.loading = false;
      } else {
        axios
          .post(process.env.VUE_APP_API + "login/sure", {
            params: this.list.length
          })
          .then(response => {
            console.log("response", this.list);
            if (this.list.length == 0) {
              this.list = response.data.data;
            } else {
              response.data.data.forEach((item, index, array) => {
                this.list.push(item);
              });
            }
            // 加载状态结束
            this.loading = false;
            // 数据全部加载完成
            if (this.list.length >= 40) {
              this.finished = true;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    }
  }
};
</script>
<style lang="less">
.van-list {
  // margin: 10px;
  height: 80px;
  width: 100%;
  .text-left {
    // height: 80px;
    margin: 2px 0 0 9px;
    float: left;
    line-height: 45px;
    font-size: 15px;
    font-family: "PingFang-SC-Medium";
    p {
      margin: 0;
      line-height: 32px;
    }
    .text-detail {
      font-size: 11px;
      color: #7b7b7d;
      line-height: 11px;
    } 
  }
}

.img-left {
  float: left;
  height: 56px;
  width: 56px;
  border-radius: 12.5px;
}

.van-cell {
  padding: 12.5px 12px;
}

.game-list{
  margin-bottom: 2px;
}
</style>