<template>
  <div class="search">
    <van-search
      v-model="value"
      show-action
      shape="round"
      @input="inputTest"
      @search="onSearch"
      clearable
      @focus="onFouce"
      :class="{'animation-pause':pause,'animation-move':move}"
    >
      <div slot="action" @click="onSearch(1)">搜索</div>
    </van-search>
    <!-- <div v-if="listShow">
      <van-list v-model="loading" :finished="finished">
        <gameList :propList="propData" />
        <van-cell
          v-for="item in list"
          :key="item.label"
          :title="item.label"
          @click="labelClick(item.label)"
        />
      </van-list>
    </div>-->
    <div class="hot-word" v-if="!listShow&&!SearchShow">
      <h4>为你推荐</h4>
      <horizontal :content="blocks" v-if="blocks.length!=0"></horizontal>
      <h4>热搜精选</h4>
      <div>
        <van-tag
          round
          v-for="item in label.filter(obj => obj.wordtype == 0).slice(0,9)"
          :key="item.hotword"
          @click="labelClick(item.hotword)"
        >{{item.hotword}}</van-tag>
      </div>
      <h4>软件</h4>
      <div>
        <van-tag
          round
          v-for="item in label.filter(obj => obj.wordtype == 1).slice(0,9)"
          :key="item.hotword"
          @click="labelClick(item.hotword)"
        >{{item.hotword}}</van-tag>
      </div>
      <h4>游戏</h4>
      <div>
        <van-tag
          round
          v-for="item in label.filter(obj => obj.wordtype == 2).slice(0,9)"
          :key="item.hotword"
          @click="labelClick(item.hotword)"
        >{{item.hotword}}</van-tag>
      </div>
    </div>
    <div class="cl" v-if="SearchShow">
      <!-- <ul>
        <li>
          <div :class="[activeClass=='all' ? 'exact' : '']" @click="addExact('all')">
            <span>全部</span>
          </div>
        </li>
        <li>
          <div :class="[activeClass=='game' ? 'exact' : '']" @click="addExact('game')">
            <span>游戏</span>
          </div>
        </li>
        <li>
          <div :class="[activeClass=='soft' ? 'exact' : '']" @click="addExact('soft')">
            <span>软件</span>
          </div>
        </li>
      </ul>-->
      <p class="count">
        共搜索到
        <span>{{pageInfo.totalRows}}</span>个结果
      </p>
      <div class="game-list">
        <van-list v-model="loading" :finished="finished" class="van-clearfix">
          <van-cell v-for="item in list" :key="item.contentId">
            <router-link :to="{ path: '/appDetails', query:{ contentid: item.contentId}}">
              <img :src="item.icon" class="img-left" />
              <div class="text-left">
                <p>{{item.title}}</p>
                <p class="text-detail">
                  <!-- <span class="download">500+下载</span> -->
                  <span
                    class="size"
                  >{{item.size=='null'?'':Math.floor(item.size/1024 * 100) / 100+'M'}}</span>
                </p>
              </div>
            </router-link>

            <div class="gift">
              <a :href="item.orderurl">
                <van-button round>下载</van-button>
              </a>
            </div>
          </van-cell>
        </van-list>
      </div>
      <van-pagination
        v-model="pageInfo.curPage"
        :total-items="Number(pageInfo.totalRows)"
        :show-page-size="5"
        force-ellipses
        @change="onSearch"
      />
    </div>
  </div>
</template>

<script>
import { Search, Tag, List, Cell, Pagination, Button } from "vant";
import axios from "axios";
import gameList from "./gameList";
import horizontal from "../main/component/horizontal"; //水平横拨
export default {
  components: {
    [Search.name]: Search,
    [List.name]: List,
    [Cell.name]: Cell,
    [Pagination.name]: Pagination,
    [Button.name]: Button,
    [Tag.name]: Tag,
    horizontal // gameList
  },
  data() {
    return {
      value: "",
      list: [],
      label: [],
      card: [],
      blocks:[], //tese = card[0].blocks
      loading: false,
      finished: true,
      listShow: false,
      activeClass: "all",
      SearchShow: false,
      pageInfo: {
        curPage: 1,
        totalPage: "10",
        totalRows: "0"
      },
      pause: false,
      move: false,
      rollHot: "",
      interval: "",
      propData: {
        price: "",
        appuid: "",
        contentId: "",
        ver: "",
        size: "",
        title: "",
        icon: "",
        orderurl: ""
      }
    };
  },
  mounted() {
    // mm.init({
    //   showtitle: true, //true：显示；false：隐藏
    //   channelid: "5410093632", //
    // });
    // mm.download("300009240030", false);
    console.log("this.$route", this.$route.query);

    var that = this;
    if (this.$route.query.key) {
      this.value = this.$route.query.key;
      // this.onSearch(this.$route.query.page);
    } else {
      axios
        .post(process.env.VUE_APP_API + "searchRollHotKeyworld.do")
        .then(response => {
          console.log("response", response.data.data);
          that.move = true;
          var i = 0;
          that.interval = setInterval(function() {
            if (i < response.data.data.length) {
              that.value = response.data.data[i];
            } else {
              i = 0;
              that.value = response.data.data[0];
            }
            i++;
          }, 4500);
        })
        .catch(function(error) {
          console.log(error);
        });
    }
    window.addEventListener(
      "popstate",
      function() {
        that.value = "";
        that.list = [];
        that.SearchShow = false;
      },
      false
    );
    axios({
      method: "post",
      url: process.env.VUE_APP_API + "searchHotKeyworld.do",
      headers: {
        "Content-Type": "application/json",
        appname: "MM7.3.2.001.01_CTAndroid_JT",
        ua: "android-24-1080x2040-RNE-AL00"
      }
    })
      .then(response => {
        this.label = response.data.hotwords;
        this.card = response.data.cards;
        this.blocks = this.card[0].blocks;
        console.log("this.label", this.label);
        // this.label.length = 9;
        console.log("this.label", this.label);
      })
      .catch(function(error) {
        console.log(error);
      });
  },
  methods: {
    onFouce() {
      if (this.interval) {
        // this.value = "";
        window.clearInterval(this.interval);
        this.pause = true;
      }
    },
    labelClick(label) {
      this.value = label;
      this.pageInfo.curPage = 1;
      this.onSearch();
    },
    onSearch(search) {
      console.log("12", this.value);
      console.log("search", search);
      this.pause = true;
      if (search) {
        this.pageInfo.curPage = search;
      }
      console.log("curPage", this.pageInfo.curPage);
      if (this.value != "") {
        axios
          .post(process.env.VUE_APP_API + "search.do", {
            keyword: this.value,
            currentPage: this.pageInfo.curPage
          })
          .then(response => {
            console.log("response");
            // window.parent.history.pushState(
            //   {},
            //   "",
            //   "#/search?key=" + this.value + "&page=" + this.pageInfo.curPage
            // );
            this.listShow = false;
            this.SearchShow = true;
            this.list = response.data.data.blocks;
            this.pageInfo = response.data.data.pageInfo;
            this.pageInfo.curPage = Number(this.pageInfo.curPage);
            // mm.download("300009240030", true);
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    inputTest() {
      if (this.value == "") {
        this.list = [];
        this.listShow = false;
        this.SearchShow = false;
      }
    },
    // onInput() {
    //   console.log("onInput", this.value);
    //   if (this.value != "") {
    //     this.listShow = true;
    //     this.SearchShow = false;
    //     axios
    //       .post(process.env.VUE_APP_API + "search.do", {
    //         keyword: this.value,
    //         currentPage: 1
    //       })
    //       .then(response => {
    //         console.log("response");
    //         if (this.listShow) {
    //           // this.list = response.data.data;
    //           this.propData = response.data.data.blocks[0];
    //         } else {
    //           this.list = [];
    //         }
    //       })
    //       .catch(function(error) {
    //         console.log(error);
    //       });
    //   } else {
    //     this.list = [];
    //     this.listShow = false;
    //     this.SearchShow = false;
    //   }
    // },
    addExact(a) {
      console.log("a", a);
      this.activeClass = a;
    }
  }
};
</script>

<style lang="less">
body {
  background-color: #fff;
}

.search {
  .slide-item .downButton {
    background-color: #ffffff;
    margin-top: 0;
    height: 8vw;
    font-size: 3.2vw;
    line-height: 7.46667vw;
  }
  .hot-word {
    margin: 0 12px;
    p {
      font-size: 12px;
      color: #1c1c1c;
    }
    .van-tag--default {
      height: 29px;
      line-height: 29px;
      padding: 0 15px;
      margin: 0 9px 9px 0;
      background-color: #f1f1f1;
      font-size: 12px;
      color: #7b7b7d;
      border-radius: 14.5px;
    }
    .slide-item .appName {
      font-size: 14px;
      font-weight: normal;
    }
  }
  .van-search__action {
    font-size: 15px;
    font-family: "PingFang";
    color: rgb(0, 0, 0);
  }
  .van-search__content {
    background-color: #f1f1f1;
  }
  a:-webkit-any-link {
    color: #323233;
  }
  .animation-move .van-field__control {
    animation: myfirst 4.5s;
    -webkit-animation: myfirst 4.5s; /* Safari and Chrome */
    animation-iteration-count: infinite;
    -webkit-animation-iteration-count: infinite; /*Safari and Chrome*/
  }
  .animation-pause .van-field__control {
    animation-play-state: paused;
  }
  .van-search__content {
    .van-cell__value,
    .van-field__body {
      height: 22px;
      input {
        line-height: normal;
      }
    }
  }
}
.cl {
  width: 100%;
  ul {
    height: 44px;
    li {
      div {
        font-size: 14px;
        font-family: "PingFang-SC-Medium";
        float: left;
        margin-left: 20px;
        width: 28px;
        line-height: 44px;
        text-align: center;
        background-color: #fff;
        color: #000;
      }

      .exact {
        color: #005bdb;
      }
    }
  }

  .count {
    margin: 0;
    padding: 0 20px;
    height: 32.5px;
    line-height: 32.5px;
    background: #eee;
    font-size: 11px;
    color: #7b7b7d;
    span {
      color: #005dbd;
    }
  }
  .exact span {
    position: relative;
    &:after {
      content: "";
      position: absolute;
      bottom: -13px;
      // left: 20px;
      height: 2px;
      width: 27.5px;
      background-color: #005bdb;
      transform: translate(-50%, 0);
      margin-left: -50%;
    }
  }
}
.van-list {
  // margin: 10px;
  height: 100%;
  width: 100%;
  .text-left {
    // height: 80px;
    margin: 2px 0 0 9px;
    max-width: 210px;
    float: left;
    line-height: 45px;
    font-size: 15px;
    font-family: "PingFang-SC-Medium";
    p {
      margin: 0;
      line-height: 32px;
    }
    .text-detail {
      font-size: 11px;
      color: #7b7b7d;
      line-height: 11px;
    }
  }
}

.img-left {
  float: left;
  height: 56px;
  width: 56px;
  border-radius: 12.5px;
}

.van-cell {
  padding: 12.5px 12px;
}

.game-list {
  margin-bottom: 2px;
}
@keyframes myfirst {
  // from {
  //   opacity:0;
  // }
  // to {
  //   opacity:1;
  // }
  0% {
    opacity: 0.5;
    padding-top: 0;
    padding-bottom: 20px;
  }
  5% {
    opacity: 1;
    padding-top: 0;
    padding-bottom: 0;
  }
  95% {
    opacity: 1;
    padding-top: 0;
    padding-bottom: 0;
  }
  100% {
    opacity: 0.7;
    padding-top: 20px;
    padding-bottom: 0;
  }
}
</style>
